@import "bootstrap/bootstrap";
@import "bootstrap/variables";
@import "bootstrap/mixins";
@import "variables";
@import "mixins";
@import "sidebar";
@import "content";
@import "auth";
@import "table";
@import "faq";
@import "header";
@import "footer";

@font-face {
	font-family: "Gilroy";
	src: url("../fonts/Gilroy-Regular.woff2") format("woff2"),
		url("../fonts/Gilroy-Regular.woff") format("woff"),
		url("../fonts/Gilroy-Regular.ttf") format("truetype");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "Gilroy";
	src: url("../fonts/Gilroy-Bold.woff2") format("woff2"),
		url("../fonts/Gilroy-Bold.woff") format("woff"),
		url("../fonts/Gilroy-Bold.ttf") format("truetype");
	font-weight: bold;
	font-style: normal;
}

body {
	font-family: "Gilroy", sans-serif;
	margin: 0;
	padding: 0;
	overflow-x: hidden;
	background: $bg-body;
}

html {
	overflow-x: hidden;
	width: 100%;
}

label {
	margin: 0;
}
button {
	transition: 0.3s all ease;
	background: 0;
	border: 0;
	&:hover,
	&:focus {
		text-decoration: none !important;
		outline: none !important;
		box-shadow: none !important;
	}
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
	padding: 0;
}
input,
a,
button {
	outline: none;
}

ul {
	padding: 0;
	margin: 0;
	li {
		list-style-type: none;
	}
}

a {
	transition: 0.2s;
	outline: none;
	&:hover {
		text-decoration: none;
	}
}

select {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

.faded {
	z-index: 6;
	opacity: 1;
}
.overlay {
	background: rgba(0, 0, 0, 0.75);
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	top: 0;
	height: 100%;
	z-index: 1;
	opacity: 1;
	transition: 0.3s;
	display: none;

	&.active {
		display: block;
	}
}

.wrapper {
	width: 100%;
}

.success-color {
	color: #02c076;
}

.danger-color {
	color: #f84960;
}
.text-color-muted {
	color: $text-color-muted;
}
